
li{
    list-style-type: none;
}
.header {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
    margin-bottom: 10px;
}
.header-bd {
    width: 100%;
    padding: 6px 9px 6px 42px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 27px 20px;
    background-position: 9px center;
}
.placeholder {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
    width: 323px;
}
.placeholder .text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.iconfont {
    font-family: h5index-iconfont;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: .2px;
    -moz-osx-font-smoothing: grayscale;
    font-size: 15px;
    padding-right: 5px;
    color: white;
}
.placeholder .text .iconfont icon-2fangdajing {
    font-size: 15px;
    display: inline-block;
    color: rgb(255, 255, 255);
    margin-right: 5px;
    padding-left: 20px;
}

.lunbo{
    padding-top: 38px;
}
.products {
    background-color: snow;
    width : 100%;
}
.product{
    margin: 0 auto;
    width: 120px;
    height: 180px;
    float: left;
    padding-left: 60px;
    padding-bottom: 0px;
    margin-bottom: 0px;
    background-color: snow;

}
.product img{
    width: 120px;
    height: 100px;
}

